      productWeight: "",
      moisture: "0",
      rawMaterialName: "",
      packingUnit: "",
      productStandard: "",
      calorie: "0",
      sodium: "0",
      note: "",
      productChangeContent: "",
      productReply: [],
      selectReply: [],
      makerOptions: [{ value: null, text: "  ." }],
      makerOptions1: [],
      makerOptions2: [],
      makerOptions3: [],
      makerOptions4: [],
      makerOptions5: [],
      makerOptions6: [],
      makerOptions7: [],
      makerOptions8: [{ value: null, text: "  ." }],
