        { value: "", text: "1. " },
        { value: " ", text: "2.  " },
        { value: " ", text: "3.  " },
        { value: "", text: "4. " },
        { value: "", text: "5. " },
        { value: "", text: "6. " },
        { value: "", text: "7. " },
      ],
      storMethodOption1: [
        { value: "1. ", text: "1. " },
        {
          value: "2.      ",
          text: "2.      ",
        },
        {
          value: "3.      ",
          text: "3.      ",
        },
        { value: "4.    ", text: "4.    " },
      ],
      storMethodOption2: [
        { value: "1. ", text: "1. " },
        {
          value: "2.      ",
          text: "2.      ",
        },
        { value: "3.    ", text: "3.    " },
      ],
      storMethodOption3: [
        { value: "1. ", text: "1. " },
        {
          value: "2.   ",
          text: "2.   ",
        },
        {
          value: "3.      ",
          text: "3.      ",
        },
        { value: "4.    ", text: "4.    " },
      ],
      storMethodOption4: [
        { value: "1. ", text: "1. " },
        {
          value: "2.      ",
          text: "2.      ",
        },
        { value: "3.   ", text: "3.   " },
        { value: "4.    ", text: "4.    " },
      ],
      storMethodOption5: [
        { value: "1. ", text: "1. " },
        {
          value: "2.   ",
          text: "2.   ",
        },
        {
          value: "3.      ",
          text: "3.      ",
        },
        { value: "4.    ", text: "4.    " },
      ],
      storMethodOption6: [
        { value: "1. ", text: "1. " },
        {
          value: "2.   ",
          text: "2.   ",
        },
        {
          value: "3.      ",
          text: "3.      ",
        },
        { value: "4.    ", text: "4.    " },
      ],
      storMethodOption7: [
        { value: "1. ", text: "1. " },
        {
          value: "2.   ",
          text: "2.   ",
        },
        {
          value: "3.      ",
          text: "3.      ",
        },
        { value: "4.    ", text: "4.    " },
      ],
      storMethodOption8: [{ value: "", text: "  ." }],
      fields: [
        { key: "selected", thClass: "w2" },
        {
          key: "productChangeReply",
          label: "  ",
          sortable: true,
          thClass: "w50",
        },
        { key: "replyDate", label: "", sortable: true, thClass: "w15" },
      ],
      fileFields: [
        {
          key: "index",
          label: "",
          sortable: true,
          thClass: "w5",
        },
        {
          key: "filename",
          label: "",
          sortable: true,
          thClass: "w50",
        },
        { key: "down", label: "", sortable: true, thClass: "w15" },
        { key: "chkDel", label: "", sortable: true, thClass: "w15" },
      ],
      confirmSkuNo: "",
      inputRead: true,
      skuNoDuplication: true,
      updateState: false,
      insertState: false,
      deleteState: false,
    };
  },
  mounted() {
    this.updateData();
    this.chkMakerList();
  },
  computed: {
    // skuNo 11 
    skuNoState() {
      return this.skuNo.length > 11 ? true : false;
    },
    //     
    selectProdut() {
      return this.$store.getters.getSkuProduct;
    },
  },
  watch: {
    //     
    selectProdut(val) {
      console.log(val);
      // this.productId = val.id;
      // this.skuNo = val.skuNo;
      // this.productName = val.productName;
      this.brandName = val.brandName;
      this.maker = val.maker;
      this.makeDate = val.makeDate;
      this.className = val.className;
      this.storMethod = val.storMethod;
      this.moisture = val.moisture;
      this.expDate = val.expDate;
      this.productWeight = val.productWeight;
      this.rawMaterialName = val.rawMaterialName;
      this.packingUnit = val.packingUnit;
      this.productStandard = val.productStandard;
      this.calorie = val.calorie;
      this.sodium = val.sodium;
      this.note = val.note;
      this.productReply = val.productChangeReplies;
    },
  },
  methods: {
    //  
    searchProduct() {
      this.searchModal = true;
      this.modalName = "searchProduct";
    },
    // sku-no  
    searchProductDup() {
      this.openSpinner();

      this.$store.commit("productStore/SET_CHECK_PRODUCT_RESULT", "3");

      this.$store
        .dispatch("productStore/CHECK_SKU_DUP", this.skuNo)
        .then((response) => {
          this.modalName = "checkSkuNo";

          if (response.data === 1) {
            this.skuNoDuplication = true;
            this.insertState = false;
            this.openModal();
            this.closeSpinner();
          } else if (response.data === 0) {
            this.skuNoDuplication = false;
            this.confirmSkuNo = this.skuNo;
            this.insertState = true;
            this.openModal();
            this.closeSpinner();
          }
          this.closeSpinner();
        })
        .catch();
    },
    //     input 
    updateData() {
      if (this.propsdata === "updateView") {
        this.productId = this.$store.state.getProduct.id;
        this.skuNo = this.$store.state.getProduct.skuNo;
        this.productName = this.$store.state.getProduct.productName;
        this.brandName = this.$store.state.getProduct.brandName;
        this.maker = this.$store.state.getProduct.maker;
        this.makeDate = this.$store.state.getProduct.makeDate;
        this.className = this.$store.state.getProduct.className;
        this.storMethod = this.$store.state.getProduct.storMethod;
        this.moisture = this.$store.state.getProduct.moisture;
        this.expDate = this.$store.state.getProduct.expDate;
        this.productWeight = this.$store.state.getProduct.productWeight;
        this.rawMaterialName = this.$store.state.getProduct.rawMaterialName;
        this.packingUnit = this.$store.state.getProduct.packingUnit;
        this.productStandard = this.$store.state.getProduct.productStandard;
        this.calorie = this.$store.state.getProduct.calorie;
        this.sodium = this.$store.state.getProduct.sodium;
        this.note = this.$store.state.getProduct.note;
        this.productReply = this.$store.state.getProduct.productChangeReplies;
        this.inputRead = false;

        this.productReply.forEach((e) => {
          let tmpData = {
            id: e.id,
            productChangeReply: e.productChangeReply,
            replyDate: e.replyDate,
            selected: false,
            createDate: e.createDate,
          };
          this.selectReply.push(tmpData);
        });

        // file   
        if (this.$store.state.getProduct.productFile.length > 0) {
          var tmpImageFile = [];
          var tmpFileData = [];

          console.log(this.$store.state.getProduct.productFile);
          this.$store.state.getProduct.productFile.forEach((element) => {
            var tmp = element.filePath.split(".");

            if (tmp[1] === "png" || tmp[1] === "jpg") {
              tmpImageFile.push(element);
            } else {
              tmpFileData.push(element);
            }
          });
          this.imgUpdate(tmpImageFile, "file");
          this.updateFileData(tmpFileData);
        }
      }
    },
    //      
    chkMakerList() {
      this.openSpinner();

      //     
      if (
        this.$store.getters["makerStore/getSelectMakerProduct"].length === 0
      ) {
        this.$store
          .dispatch("makerStore/SELECT_MAKER", {
            makerName: "",
            makerAddress: "",
            makerPerson: "",
            makerPhone: "",
            className: [],
            newProduct: "product",
          })
          .then((response) => {
            if (response.data === 0) {
              console.log(" ");
              console.log(response);
            } else {
              this.saveMakerList();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        this.saveMakerList();
      }
    },
    //    
    saveMakerList() {
      //    
      this.$store.getters["makerStore/getSelectMakerProduct"].forEach(
        (element) => {
          switch (element.className) {
            case "":
              this.makerOptions1.push({
                value: element.makerName,
                text: element.makerName,
              });
              break;
            case " ":
              this.makerOptions2.push({
                value: element.makerName,
                text: element.makerName,
              });
              break;
            case " ":
              this.makerOptions3.push({
                value: element.makerName,
                text: element.makerName,
              });
              break;
            case "":
              this.makerOptions4.push({
                value: element.makerName,
                text: element.makerName,
              });
              break;
            case "":
              this.makerOptions5.push({
                value: element.makerName,
                text: element.makerName,
              });
              break;
            case "":
              this.makerOptions6.push({
                value: element.makerName,
                text: element.makerName,
              });
              break;
          }
        }
      );

      this.closeSpinner();
    },
    //  
    selectReplyData(item) {
      this.selectReply.forEach((element, index) => {
        if (element.id === item.id) {
          this.selectReply[index].selected = item.selected;
        }
      });
    },
    //   
    classNameChange() {
      if (this.className !== "") {
        this.calorie = "0";
        this.sodium = "0";
        this.moisture = "0";
      }

      this.maker = null;
    },
    //  
    insertProduct() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();

        this.$store.commit("productStore/SET_INSERT_PRODUCT", "3");

        this.imgFiles.forEach((element) => {
          this.formData.append("file", element["file"]);
        });

        // this.formData.append("fileData", this.fileData);
        this.fileData.forEach((element) => {
          this.formData.append("file", element["file"]);
        });

        let data = {
          skuNo: this.skuNo,
          productName: this.productName,
          brandName: this.brandName,
          maker: this.maker,
          makeDate: this.makeDate,
          className: this.className,
          storMethod: this.storMethod,
          moisture: this.moisture,
          expDate: this.expDate,
          productWeight: this.productWeight,
          rawMaterialName: this.rawMaterialName,
          packingUnit: this.packingUnit,
          productStandard: this.productStandard,
          calorie: this.calorie,
          sodium: this.sodium,
          note: this.note,
        };

        this.formData.append(
          "data",
          new Blob([JSON.stringify(data)], { type: "application/json" })
        );

        this.formData.append(
          "productChangeContent",
          new Blob([JSON.stringify(this.productChangeContent)], {
            type: "application/json",
          })
        );

        this.$store
          .dispatch("productStore/INSERT_PRODUCT", this.formData)
          .then((response) => {
            this.modalName = "insertProduct";

            if (response.data === 1) {
              this.insertState = true;
              this.openModal();
              this.closeSpinner();
            } else if (response.data === 0) {
              this.insertState = false;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });

        this.formData = new FormData();
      }
    },
    //  
    updateProduct() {
      this.openSpinner();
      this.$store.commit("productStore/SET_UPDATE_PRODUCT", "3");

      this.imgFiles.forEach((element) => {
        if (element["file"]) {
          this.formData.append("file", element["file"]);
        } else if (element["imgId"]) {
          this.formData.append("fileId", element["imgId"]);
        }
      });

      this.fileData.forEach((element) => {
        if (element["file"]) {
          this.formData.append("file", element["file"]);
        } else if (element["fileId"]) {
          this.formData.append("fileId", element["fileId"]);
        }
      });

      let data = {
        id: this.productId,
        skuNo: this.skuNo,
        productName: this.productName,
        brandName: this.brandName,
        maker: this.maker,
        makeDate: this.makeDate,
        className: this.className,
        storMethod: this.storMethod,
        moisture: this.moisture,
        expDate: this.expDate,
        productWeight: this.productWeight,
        rawMaterialName: this.rawMaterialName,
        packingUnit: this.packingUnit,
        productStandard: this.productStandard,
        calorie: this.calorie,
        sodium: this.sodium,
        note: this.note,
      };

      this.formData.append(
        "data",
        new Blob([JSON.stringify(data)], { type: "application/json" })
      );
      this.formData.append(
        "productId",
        new Blob([JSON.stringify(this.productId)], {
          type: "application/json",
        })
      );
      this.formData.append(
        "productChangeContent",
        new Blob([JSON.stringify(this.productChangeContent)], {
          type: "application/json",
        })
      );

      this.$store
        .dispatch("productStore/UPDATE_PRODUCT", this.formData)
        .then((response) => {
          this.modalName = "updateProduct";
          console.log(response);

          if (response.data === 1) {
            this.updateState = true;
            this.openModal();
            this.closeSpinner();
          } else if (response.data === 0) {
            this.updateState = false;
            this.openModal();
            this.closeSpinner();
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //  
    deleteProduct() {
      this.openSpinner();

      this.$store.commit("productStore/SET_DELETE_PRODUCT", "3");
      this.$store
        .dispatch("productStore/DELETE_PRODUCT", {
          id: this.productId,
          skuNo: this.skuNo,
        })
        .then((response) => {
          this.modalName = "deleteProduct";

          if (response.data === 1) {
            this.deleteState = true;
            this.openModal();
            this.closeSpinner();
          } else if (response.data === 0) {
            this.deleteState = false;
            this.openModal();
            this.closeSpinner();
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //  
    updateProductReply() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();

        this.$store.commit("productStore/SET_UPDATE_PRODUCT_REPLY", "3");

        var tmpReply = [];
        this.productReply.forEach((element) => {
          if (element.selected) {
            tmpReply.push({
              replyId: element.id,
              changeReplyData: element.productChangeReply,
            });
          }
        });

        let data = {
          id: this.productId,
          replyDataList: tmpReply,
        };

        this.$store
          .dispatch("productStore/UPDATE_PRODUCT_REPLY", data)
          .then((response) => {
            this.modalName = "updateProductReply";

            if (response.data === 1) {
              this.updateState = true;
              this.openModal();
              this.closeSpinner();
            } else if (response.data === 0) {
              this.updateState = false;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    //   
    deleteProductReply() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();

        var tmpReply = [];
        this.productReply.forEach((element) => {
          if (element.selected) {
            tmpReply.push(element.id);
          }
        });

        let data = {
          productId: this.productId,
          productReplyId: tmpReply,
        };

        this.$store.commit("productStore/SET_DELETE_PRODUCT_REPLY", "3");
        this.$store
          .dispatch("productStore/DELETE_PRODUCT_REPLY", data)
          .then((response) => {
            this.modalName = "deleteProductReply";

            if (response.data === 1) {
              this.deleteState = true;
              this.openModal();
              this.closeSpinner();
            } else if (response.data === 0) {
              this.deleteState = false;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    //     
    inpuReadMode() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      } else {
        this.inputRead = true;
      }
    },
    //     
    roturInit() {
      this.$router.push("/productSel");
    },
  },
};
</script>

