<template>
  <div class="body">
    <b-overlay :show="spinnerState" rounded="sm">
      <div class="title"> </div>
      <select-sales
        :propsdata="message"
        @spinnerStart="openSpinner"
      ></select-sales>
      <div class="row">
        <div class="col-md-12">
          <b-button variant="primary" class="right-box" v-on:click="download"
            ></b-button
          >
        </div>
      </div>
      <div>
        <b-table
          sticky-header="72vh"
          responsive
          striped
          hover
          :items="getSelectSales"
          :fields="fields"
          label-sort-asc=""
          label-sort-desc=""
          label-sort-clear=""
          @row-dblclicked="inspectDetails"
        ></b-table>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import SelectSales from "../../components/select/SelectSales.vue";
import * as XLSX from "xlsx";
// import { mapGetters } from 'vuex'

export default {
  components: {
    SelectSales,
  },
  data() {
    return {
      message: "testSelView",
      getSelectSales: [],
      fields: [
        { key: "skuNo", label: "SKU-NO", sortable: true },
        { key: "productName", label: "", sortable: true },
        { key: "jan", label: "1", sortable: true },
        { key: "feb", label: "2", sortable: true },
        { key: "mar", label: "3", sortable: true },
        { key: "apr", label: "4", sortable: true },
        { key: "may", label: "5", sortable: true },
        { key: "jun", label: "6", sortable: true },
        { key: "jul", label: "7", sortable: true },
        { key: "aug", label: "8", sortable: true },
        { key: "sep", label: "9", sortable: true },
        { key: "oct", label: "10", sortable: true },
        { key: "nov", label: "11", sortable: true },
        { key: "december", label: "12", sortable: true },
      ],
      spinnerState: false,
    };
  },
  created() {},
  mounted() {
    // this.openSpinner();
  },
  computed: {
    // ...mapGetters('makerStore', [
    //   'getSelectSales'
    // ]),
    SelectMaker() {
      return this.$store.getters["salesStore/getSelectSales"];
    },
  },
  watch: {
    //     
    SelectMaker(val) {
      this.getSelectSales = val;
      this.closeSpinner();
    },
  },
  methods: {
    // 
    download() {
      const workBook = XLSX.utils.book_new();
      const workSheet = XLSX.utils.json_to_sheet(this.getSelectSales);
      XLSX.utils.book_append_sheet(workBook, workSheet, " ");
      XLSX.writeFile(workBook, " .xlsx");
    },
    //   
    inspectDetails(item) {
      this.$store.commit("SET_MAKER", "");

      //         
      this.$store.commit("SET_MAKER", item);
      this.$router.push(`/makerUp/${item.id}`);
    },
    //  
    openSpinner() {
      this.spinnerState = true;
    },
    //  
    closeSpinner() {
      this.spinnerState = false;
    },
  },
};
</script>

<style scoped>
.right-box {
  float: right;
  width: auto;
}
</style>