<!--    -->
<template>
  <div>
    <select-slot>
      <div slot="top" class="row">
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-input
              type="text"
              v-model="makerName"
              placeholder=""
              value=""
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-input
              type="text"
              v-model="makerAddress"
              placeholder=""
              value=""
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend=" ">
            <b-form-input
              type="text"
              v-model="makerPerson"
              placeholder=""
              value=""
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend=" ">
            <b-form-input
              type="text"
              v-model="makerPhone"
              placeholder=" ."
              maxlength="14"
              oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
      </div>
      <div slot="middle" class="row">
        <div class="col-md-10 mb-3">
          <b-input-group prepend="" v-slot="{ ariaDescribedby }">
            <b-form-checkbox-group
              class="chkBoxGroup"
              id="checkbox-group-1"
              v-model="className"
              :aria-describedby="ariaDescribedby"
              name="flavour-1"
            >
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
              <b-form-checkbox class="chkBox" value=" ">
                 
              </b-form-checkbox>
              <b-form-checkbox class="chkBox" value=" ">
                 
              </b-form-checkbox>
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
              <b-form-checkbox class="chkBox" value="">
                
              </b-form-checkbox>
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
            </b-form-checkbox-group>
          </b-input-group>
        </div>
        <div class="col-md-2 mb-3" v-if="this.$route.name !== 'inspectSelView'">
          <b-button
            variant="primary"
            class="left-box"
            @click="
              spinnerStart();
              searchData();
            "
            > </b-button
          >
        </div>
      </div>
    </select-slot>
  </div>
</template>

<script>
import selectSlot from "./SelectProductSlot.vue";

export default {
  components: {
    selectSlot,
  },
  data() {
    return {
      makerId: "",
      makerName: "",
      makerAddress: "",
      makerPerson: "",
      makerPhone: "",
      selectParam: [],
      className: [],
    };
  },
  created() {},
  methods: {
    //  
    searchData() {
      this.$store.dispatch("makerStore/SELECT_MAKER", {
        makerName: this.maker,
        makerAddress: this.makerAddress,
        makerPerson: this.makerPerson,
        makerPhone: this.makerPhone,
        className: this.className,
      });
    },
    //    
    spinnerStart() {
      this.$emit("spinnerStart");
    },
  },
  mounted() {
    //     
    this.searchData();
  },
};
</script>

<style scoped>
.btn_margin {
  margin-right: 5px;
}
.left-box {
  float: right;
  width: auto;
}
.custom-control custom-control-inline custom-checkbox {
  margin-left: 10px;
}
.chkBox {
  margin-left: 15px;
}
</style>
