export const adminChkMixin = {
    data() {
        return {
            adminChkFlag : false
        }
    },
    methods: {
        //     
        adminChk() {
            //     
            let checkUser = this.$store.getters["loginStore/getUserLogin"];
            if (checkUser.length === 0 || checkUser.data.body.data.role !== "ADMIN") {
                this.adminChkFlag = true;
            }
            else {
                this.adminChkFlag = false
            }
        },
    }
}