<template>
  <div class="body">
    <b-overlay :show="spinnerState" rounded="sm">
      <div class="title" v-if="propsdata === 'insertSales'">
          ()
      </div>
      <div class="title" v-else>    </div>
      <div class="row" v-if="propsdata === 'insertSales'">
        <div class="col-md-6 mb-3">
          <b-input-group prepend=" ">
            <input
              type="file"
              @change="readFile"
              accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            />
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-datepicker v-model="tmpMonth"></b-form-datepicker>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-button
            variant="primary"
            class="rightBox"
            v-b-modal.insert-modal
            @click="checkDate"
            :disabled="!excelFileFlag"
          >
             
          </b-button>
        </div>
      </div>
      <div class="row" v-else>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-datepicker v-model="findDate"></b-form-datepicker>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="SKU-NO">
            <b-form-input
              type="text"
              v-model="skuNo"
              placeholder=""
              value=""
              maxlength="12"
              oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\\..*)\\./g, '$1');"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-input
              type="text"
              v-model="productName"
              placeholder=""
              value=""
            ></b-form-input>
          </b-input-group>
        </div>

        <div class="col-md-3 mb-3">
          <b-button
            variant="primary"
            class="rightBox"
            v-b-modal.update-modal
            :disabled="!getSalesDataFlag"
          >
             
          </b-button>
          <b-button
            variant="danger"
            class="rightBox"
            v-b-modal.delete-modal
            :disabled="!getSalesDataFlag"
          >
             
          </b-button>
          <b-button variant="primary" class="rightBox" @click="selectSalesData">
             
          </b-button>
        </div>
      </div>
      <div class="row" v-if="propsdata === 'updateSales'">
        <div class="col-md-3 mb-3">{{ salesMonth }}  </div>
      </div>
      <div>
        <b-table
          striped
          hover
          :items="getSalesData"
          :fields="fields"
          label-sort-asc=""
          label-sort-desc=""
          label-sort-clear=""
        >
          <template v-slot:cell(salesVolumn)="row">
            <b-form-input v-model="row.item.salesVolumn" />
          </template>
        </b-table>
      </div>
    </b-overlay>

    <b-modal id="insert-modal">
      <!-- <template #modal-title>   </template> -->
      <div class="my-4">{{ modalMonth }}  ?</div>
      <template #modal-footer>
        <div class="d-block text-center">
          <b-button
            variant="primary"
            class="frightBox"
            @click="insertSalesData(), $bvModal.hide('insert-modal')"
            :disabled="spinnerState"
          >
            
          </b-button>
          <b-button
            variant="primary"
            class="rightBox"
            @click="$bvModal.hide('insert-modal')"
          >
            
          </b-button>
        </div>
      </template>
    </b-modal>

    <b-modal id="update-modal">
      <!-- <template #modal-title>   </template> -->
      <div class="my-4"> ?</div>
      <template #modal-footer>
        <div class="d-block text-center">
          <b-button
            variant="primary"
            class="frightBox"
            @click="updateSalesData(), $bvModal.hide('update-modal')"
            :disabled="spinnerState"
          >
            
          </b-button>
          <b-button
            variant="primary"
            class="rightBox"
            @click="$bvModal.hide('insert-modal')"
          >
            
          </b-button>
        </div>
      </template>
    </b-modal>

    <b-modal id="delete-modal">
      <!-- <template #modal-title>   </template> -->
      <div class="my-4"> ?</div>
      <template #modal-footer>
        <div class="d-block text-center">
          <b-button
            variant="primary"
            class="frightBox"
            @click="deleteSalesData(), $bvModal.hide('delete-modal')"
            :disabled="spinnerState"
          >
            
          </b-button>
          <b-button
            variant="primary"
            class="rightBox"
            @click="$bvModal.hide('insert-modal')"
          >
            
          </b-button>
        </div>
      </template>
    </b-modal>
    <!-- . -->
    <div>
      <confirm-modal @close="closeModal" v-if="modal">
        <div v-if="modalName === 'insertSales'">
          <div v-if="insertState === 1">
            <p>  .</p>
          </div>
          <div v-else-if="insertState === 2">
            <p>
                  .    
                .
            </p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'updateMonthSales'">
          <div v-if="updateState">
            <p>  .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'deleteMonthSales'">
          <div v-if="deleteState">
            <p>  .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'noAdmin'">
          <p>   , ,   .</p>
        </div>

        <template
          slot="footer"
          v-if="modalName === 'insertSales' && insertState"
        >
          <button @click="roturInit('salesSel')"></button>
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'updateMonthSales' && updateState"
        >
          <button @click="roturInit('salesSel')"></button>
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'deleteMonthSales' && deleteState"
        >
          <button @click="roturInit('salesSel')"></button>
        </template>
        <template slot="footer" v-else>
          <button @click="modalText"></button>
        </template>
      </confirm-modal>
    </div>
  </div>
</template>

<script>
import * as XLSX from "xlsx";
import confirmModal from "../../components/modal/ConfirmModal.vue";
import { modalMixin } from "../../mixins/modalMixin.js";
import { spinnerMixin } from "../../mixins/spinnerMixin.js";
import { adminChkMixin } from "../../mixins/adminChkMixin.js";
import dayjs from "dayjs";

export default {
  components: {
    confirmModal,
  },
  mixins: [modalMixin, spinnerMixin, adminChkMixin],
  props: ["propsdata"],
  data() {
    return {
      skuNo: "",
      productName: "",
      findDate: "",
      salesMonth: "",
      modalMonth: "",
      tmpMonth: "",
      getSalesData: [],
      getSalesDataFlag: false,
      excelFileFlag: false,
      insertState: "",
      updateState: "",
      deleteState: "",
      fields: [
        { key: "skuNo", label: "skuNo", sortable: true, thClass: "w15" },
        { key: "productName", label: "", sortable: true, thClass: "w15" },
        { key: "salesVolumn", label: "", sortable: true, thClass: "w30" },
      ],
      spinnerState: false,
    };
  },
  mounted() {
    this.setDate();
  },
  methods: {
    //    
    readFile(event) {
      const file = event.target.files[0];
      let reader = new FileReader();
      let tmpResult = {};
      reader.onload = () => {
        let data = reader.result;
        let workbook = XLSX.read(data, { type: "binary" });
        workbook.SheetNames.forEach((sheetName) => {
          workbook.Sheets[sheetName].A1.w = "skuNo";
          workbook.Sheets[sheetName].B1.w = "productName";
          workbook.Sheets[sheetName].C1.w = "salesVolumn";

          const roa = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName]);
          tmpResult = roa;
        });
        this.excelJsonData = tmpResult;
        this.getSalesData = tmpResult;
        this.excelFileFlag = true;
      };
      reader.readAsBinaryString(file);
    },
    //   
    setDate() {
      var thisday = new Date();

      // , ,  
      var year = thisday.getFullYear();
      var month = ("0" + (thisday.getMonth() + 1)).slice(-2);
      var day = ("0" + thisday.getDate()).slice(-2);

      //   (ex - '2021-10-08')
      this.tmpMonth = year + "-" + month + "-" + day;
      //   (ex - '2021-10-08')
      this.findDate = year + "-" + month + "-" + day;
    },
    //    
    checkDate() {
      this.modalMonth =
        this.tmpMonth.slice(0, 4) + " " + this.tmpMonth.slice(5, 7) + "";

      this.salesMonth = this.tmpMonth.slice(0, 7) + "-01";
    },
    //  
    insertSalesData() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("salesStore/SET_INSERT_MONTH_SALES", 3);

        let data = {
          salesMonth: this.salesMonth,
          salesData: this.getSalesData,
        };

        this.$store
          .dispatch("salesStore/INSERT_MONTH_SALES", data)
          .then((response) => {
            this.modalName = "insertSales";

            //   
            if (response.data === 1) {
              
              this.insertState = 1;
            }
            //   
            else if (response.data === 0) {
              this.insertState = 0;
            } else if (response.data === 2) {
              this.insertState = 2;
            }
            this.openModal();
            this.closeSpinner();
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    //  
    selectSalesData() {
      this.openSpinner();
      //    
      this.salesMonth =
        this.findDate.slice(0, 4) + " " + this.findDate.slice(6, 7) + "";
      this.findDate = this.findDate.slice(0, 7) + "-01";
      var bfdate = dayjs(this.findDate);

      this.$store
        .dispatch("salesStore/SELECT_MONTH_SALES", {
          skuNo: this.skuNo,
          productName: this.productName,
          findDate: bfdate.format("YYYY-MM-DD"),
        })
        .then((response) => {
          this.getSalesData = response.data;
          if (this.getSalesData.length > 0) {
            this.getSalesDataFlag = true;
          } else {
            this.getSalesDataFlag = false;
          }
          this.closeSpinner();
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //  
    updateSalesData() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("salesStore/SET_UPDATE_MONTH_SALES", 3);

        let data = {
          salesMonth: this.getSalesData[0].salesMonth,
          salesData: this.getSalesData,
        };

        this.$store
          .dispatch("salesStore/UPDATE_MONTH_SALES", data)
          .then((response) => {
            this.modalName = "updateMonthSales";

            //   
            if (response.data === 1) {
              this.updateState = 1;
            }
            //   
            else if (response.data === 0) {
              this.updateState = 0;
            }
            this.openModal();
            this.closeSpinner();
          })
          .catch((error) => {
            console.log(error);
          });

        // this.formData = new FormData();
      }
    },
    //  
    deleteSalesData() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("salesStore/SET_DELETE_MONTH_SALES", 3);

        var tmpIds = [];

        this.getSalesData.forEach((element) => {
          tmpIds.push(element.id);
        });

        let data = {
          ids: tmpIds,
        };

        this.$store
          .dispatch("salesStore/DELETE_MONTH_SALES", data)
          .then((response) => {
            this.modalName = "deleteMonthSales";

            //   
            if (response.data === 1) {
              
              this.deleteState = 1;
            }
            //   
            else if (response.data === 0) {
              
              this.deleteState = 0;
            }
            this.openModal();
            this.closeSpinner();
          })
          .catch((error) => {
            
            console.log(error);
          });

        // this.formData = new FormData();
      }
    },
  },
};
</script>

<style scoped>
.rightBox {
  float: right;
  margin-left: 10px;
}
</style>