<template>
  <div class="loginBody">
    <div class="loginForm">
      <div class="loginText">Login</div>

      <b-form-input
        class="loginInput"
        type="text"
        v-model="userId"
        placeholder=""
        value=""
      ></b-form-input>
      <b-form-input
        class="loginInput"
        type="password"
        v-model="userPassword"
        placeholder=""
        value=""
        @keyup.enter="userLogin()"
      ></b-form-input>
      <b-button variant="primary" class="loginInput" @click="userLogin()">
        
      </b-button>
      <!-- <b-button variant="primary" class="loginInput">  </b-button> -->
      <!-- <router-link style="float: left" class="linkFont" to="/productSel">
         </router-link> -->
      <!-- <router-link style="float: right" class="linkFont" to="/inspectUp">
         ?</router-link
      > -->
    </div>
    <!--    -->
    <div>
      <confirm-modal @close="closeModal" v-if="modal">
        <div v-if="modalName === '200'">
          <p>{{ this.modalMessage }}</p>
        </div>
        <div v-else-if="modalName === '401'">
          <p>    .</p>
        </div>
        <div v-else-if="modalName === '403'">
          <p> .</p>
        </div>
        <div v-else>
          <p> .    .</p>
        </div>
        <template slot="footer">
          <b-button variant="primary" @click="modalText"></b-button>
        </template>
      </confirm-modal>
    </div>
  </div>
</template>

<script>
import confirmModal from "../../components/modal/ConfirmModal.vue";

export default {
  data() {
    return {
      userId: "",
      userPassword: "",
      modal: false,
      modalName: "",
      spinnerState: false,
      modalMessage: "",
    };
  },
  mounted() {
    this.chkLogin();
  },
  components: {
    confirmModal,
  },
  methods: {
    chkLogin() {
      console.log("");
      if (
        this.$store.getters["loginStore/getUserLogin"].status === 200 &&
        this.$store.getters["loginStore/getUserLogin"].data.body.result ===
          "success"
      ) {
        this.roturInit();
      }
    },
    userLogin() {
      this.openSpinner();

      this.$store
        .dispatch("loginStore/USER_LOGIN", {
          userId: this.userId,
          userPassword: this.userPassword,
        })
        .then((response) => {
          //   
          if (response.status === 200) {
            //  
            if (response.data.body.result === "fail") {
              this.modalName = "200";
              this.modalMessage = response.data.body.massage;
              this.openModal();
            }
            //   
            else {
              this.$store.dispatch("makerStore/SELECT_MAKER", {
                makerName: "",
                makerAddress: "",
                makerPerson: "",
                makerPhone: "",
                className: [],
                newProduct: "product",
              });

              this.roturInit();
            }
          }
          //    
          else if (response.status === 401) {
            this.modalName = "401";
            this.openModal();
          } else {
            this.modalName = "500";
            this.openModal();
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //  
    openModal() {
      this.closeSpinner();
      this.modal = true;
    },
    //  
    closeModal() {
      this.modal = false;
    },
    //   
    roturInit() {
      this.closeSpinner();
      this.$router.push("/productSel");
      // this.$router.go();
    },
    //  
    openSpinner() {
      this.spinnerState = true;
    },
    //  
    closeSpinner() {
      this.spinnerState = false;
    },
    modalText() {
      this.closeSpinner();
      this.modal = false;
    },
  },
  computed: {},
  watch: {},
};
</script>

<style scoped>
.loginBody {
  width: 100vw;
  height: 100vh;
  background-color: #4b89dc;
}
.loginText {
  font-size: 40px;
  padding-bottom: 10px;
}
.loginForm {
  width: 400px;
  height: 350px;
  font-size: 12px;
  border: 0;
  background-color: #e2e2e2;
  padding: 20px;
  margin: auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.loginInput {
  width: 100%;
  margin-bottom: 15px;
  border: none;
  border-radius: 5px;
}
.linkFont {
  font-size: 14px;
  color: gray;
  margin: 0 auto;
}
</style>
