<template>
  <div class="body">
    <b-overlay :show="spinnerState" rounded="sm">
      <div class="title"> </div>
      <select-product
        :propsdata="message"
        @spinnerStart="openSpinner"
      ></select-product>
      <div class="row">
        <div class="col-md-12">
          <b-button variant="primary" class="right-box" v-on:click="download"
            ></b-button
          >
        </div>
      </div>
      <div>
        <b-table
          sticky-header="72vh"
          responsive
          striped
          hover
          :items="getSelectClaim"
          :fields="fields"
          label-sort-asc=""
          label-sort-desc=""
          label-sort-clear=""
          @row-dblclicked="claimDetails"
        ></b-table>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import SelectProduct from "../../components/select/SelectProduct.vue";
import * as XLSX from "xlsx";

export default {
  components: {
    SelectProduct,
  },
  data() {
    return {
      message: "testSelView",
      getSelectClaim: [],
      fields: [
        {
          key: "claimDate",
          label: "  ",
          sortable: true,
          thClass: "w10",
        },
        {
          key: "product.skuNo",
          label: "skuNo",
          sortable: true,
          thClass: "w10",
        },
        {
          key: "product.productName",
          label: "",
          sortable: true,
          thClass: "w20",
        },
        {
          key: "product.className",
          label: "",
          sortable: true,
          thClass: "w10",
        },
        {
          key: "claimDecide",
          label: " ",
          sortable: true,
          thClass: "w10",
        },
        {
          key: "claimContent",
          label: " ",
          sortable: true,
          thClass: "w20",
        },
        {
          key: "lotDate",
          label: "(LOT)",
          sortable: true,
          thClass: "w10",
        },
        { key: "recall", label: "", sortable: true, thClass: "w10" },
      ],
      spinnerState: false,
    };
  },
  created() {},
  mounted() {
    this.openSpinner();
  },
  computed: {
    Selectclaim() {
      return this.$store.getters["claimStore/getSelectClaim"];
    },
  },
  watch: {
    //     
    Selectclaim(val) {
      this.getSelectClaim = val;
      this.closeSpinner();
    },
  },
  methods: {
    // 
    download() {
      const workBook = XLSX.utils.book_new();
      const workSheet = XLSX.utils.json_to_sheet(this.getSelectClaim);
      XLSX.utils.book_append_sheet(workBook, workSheet, " ");
      XLSX.writeFile(workBook, " .xlsx");
    },
    //   
    claimDetails(item) {
      this.$store.commit("SET_CLAIM", "");

      //         
      this.$store.commit("SET_CLAIM", item);
      this.$router.push(`/claimUp/${item.id}`);
    },
    //  
    openSpinner() {
      this.spinnerState = true;
    },
    //  
    closeSpinner() {
      this.spinnerState = false;
    },
  },
};
</script>

<style scoped>
.right-box {
  float: right;
  width: auto;
}
</style>