<!--    -->
<template>
  <div>
    <select-slot>
      <div slot="top" class="row">
        <div class="col-md-3 mb-3">
          <b-input-group prepend="SKU-NO">
            <b-form-input
              type="text"
              v-model="skuNo"
              placeholder=" ."
              value=""
              maxlength="13"
              oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-input
              type="text"
              v-model="productName"
              placeholder=""
              value=""
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-input
              type="text"
              v-model="brandName"
              placeholder=""
              value=""
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
        <div class="col-md-3 mb-3">
          <b-input-group prepend="">
            <b-form-input
              type="text"
              v-model="maker"
              placeholder=""
              value=""
              @keyup.enter="searchData()"
            ></b-form-input>
          </b-input-group>
        </div>
      </div>
      <div slot="middle" class="row">
        <div class="col-md-10 mb-3">
          <b-input-group prepend="" v-slot="{ ariaDescribedby }">
            <b-form-checkbox-group
              class="chkBoxGroup"
              id="checkbox-group-1"
              v-model="className"
              :aria-describedby="ariaDescribedby"
              name="flavour-1"
            >
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
              <b-form-checkbox class="chkBox" value=" ">
                 
              </b-form-checkbox>
              <b-form-checkbox class="chkBox" value=" ">
                 
              </b-form-checkbox>
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
              <b-form-checkbox class="chkBox" value="">
                
              </b-form-checkbox>
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
              <b-form-checkbox class="chkBox" value=""
                ></b-form-checkbox
              >
            </b-form-checkbox-group>
          </b-input-group>
        </div>
        <div
          class="col-md-2 mb-3"
          v-if="
            this.$route.name === 'selectAllProduct' ||
            this.$route.name === 'inspectInView' ||
            this.$route.name === 'claimInView' ||
            this.$route.name === 'salesProductInView' ||
            this.$route.name === 'salesProductUp' ||
            this.$route.name === 'productInsert'
          "
        >
          <b-button variant="primary" class="left-box" @click="searchData()"
            > </b-button
          >
        </div>
      </div>
      <div
        slot="bottom"
        v-if="
          this.$route.name === 'inspectSelView' ||
          this.$route.name === 'claimSelView'
        "
        class="row"
      >
        <div class="col-md-6 mb-3">
          <b-input-group prepend="">
            <b-form-datepicker
              id="datepicker-dateformat1"
              v-model="beforeDate"
            ></b-form-datepicker>
            <b-form-datepicker
              id="datepicker-dateformat2"
              v-model="afterDate"
            ></b-form-datepicker>
          </b-input-group>
        </div>
        <div class="col-md-4 mb-3">
          <b-button variant="primary" class="btn_margin" @click="prevWeek"
            >1 </b-button
          >
          <b-button variant="primary" class="btn_margin" @click="prevMonth"
            >1 </b-button
          >
          <b-button variant="primary" class="btn_margin" @click="prevThreeMonth"
            >3 </b-button
          >
        </div>
        <div class="col-md-2 mb-3" v-if="this.$route.name === 'inspectSelView'">
          <b-button variant="primary" class="left-box" @click="searchData()"
            > </b-button
          >
        </div>
        <div class="col-md-2 mb-3" v-else>
          <b-button
            variant="primary"
            class="left-box"
            @click="
              spinnerStart();
              searchData();
            "
            > </b-button
          >
        </div>
      </div>
    </select-slot>
    <confirm-modal @close="closeModal" v-if="modal">
      <div v-if="modalName === 'noConfirmDate'">
        <p>   .     .</p>
      </div>
      <template slot="footer" v-else>
        <button @click="modalText"></button>
      </template>
    </confirm-modal>
  </div>
</template>

<script>
import selectSlot from "../select/SelectProductSlot.vue";
import dayjs from "dayjs";
import isSameOrBefore from "dayjs/plugin/isSameOrBefore";
import confirmModal from "../modal/ConfirmModal.vue";
import { dateSearchMixin } from "../../mixins/dateSearchMixin";

dayjs.extend(isSameOrBefore);

export default {
  components: {
    selectSlot,
    confirmModal,
  },
  mixins: [dateSearchMixin],
  data() {
    return {
      skuNo: "",
      productName: "",
      brandName: "",
      maker: "",
      header: ["skuNo", "", "", "", ""],
      selectParam: [],
      className: [],
      modal: false,
      modalName: "",
    };
  },

  methods: {
    //  
    searchData() {
      console.log(this.$route.name);
      this.spinnerStart();
      if (
        this.$route.name === "selectAllProduct" ||
        this.$route.name === "inspectInView" ||
        this.$route.name === "claimInView" ||
        this.$route.name === "salesProductInView" ||
        this.$route.name === "salesProductUp" ||
        this.$route.name === "productInsert" || 
        this.$route.name === "inspectUpView"
      ) {
        //   
        let data = {
          page: 0,
          name: this.$route.name,
          skuNo: this.skuNo,
          productName: this.productName,
          brandName: this.brandName,
          maker: this.maker,
          className: this.className,
        };
        //   
        this.$store.commit("productStore/SET_SEARCH_DATA", data);

        this.$store.dispatch("productStore/SELECT_PRODUCT", data);
      } else {
        var bfdate = dayjs(this.beforeDate);
        var afdate = dayjs(this.afterDate);

        if (bfdate.isSameOrBefore(this.afterDate, "day")) {
          let data = {
            page: 0,
            name: this.$route.name,
            skuNo: this.skuNo,
            productName: this.productName,
            brandName: this.brandName,
            maker: this.maker,
            className: this.className,
            beforeDate: this.beforeDate,
            afterDate: afdate.add(1, "day").format("YYYY-MM-DD"),
          };

          if (this.$route.name === "inspectSelView") {
            //   
            this.$store.commit("inspectStore/SET_SEARCH_DATA", data);
            this.$store.dispatch("inspectStore/SELECT_INSPECT", data);
          } else {
            this.$store.dispatch("claimStore/SELECT_CLAIM", data);
          }
        } else {
          this.modalName = "noConfirmDate";
          this.modal = true;
        }
      }
    },
    //  
    openModal() {
      this.modal = true;
    },
    //  
    closeModal() {
      this.modal = false;
    },
    //    
    spinnerStart() {
      this.$emit("spinnerStart");
    },
  },
  mounted() {
    //     
    this.prevWeek();
    //     
    this.searchData();
  },
};
</script>

<style scoped>
.btn_margin {
  margin-right: 5px;
}
.left-box {
  float: right;
  width: auto;
}
.custom-control custom-control-inline custom-checkbox {
  margin-left: 10px;
}
.chkBox {
  margin-left: 15px;
}
</style>
