<template>
  <div>
    <b-overlay :show="spinnerState" rounded="sm">
      <div :class="[inputRead ? 'divEnable' : 'divDisable']">
        <div class="row">
          <div class="col-md-6 mb-3">
            <b-input-group prepend="SKU-NO">
              <b-form-input
                type="text"
                v-model="skuNo"
                placeholder=""
                value=""
                maxlength="12"
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
                required
                readonly
              ></b-form-input>
              <b-input-group-prepend>
                <b-button variant="primary" @click="searchProduct">
                  
                </b-button>
              </b-input-group-prepend>
            </b-input-group>
          </div>
          <div class="col-md-6 mb-3">
            <b-input-group prepend="">
              <b-form-input
                type="text"
                v-model="productName"
                placeholder=""
                value=""
                readonly
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6 mb-3">
            <b-input-group prepend="">
              <b-form-input
                type="text"
                v-model="orderNum"
                placeholder=""
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-6 mb-3">
            <b-input-group prepend=" ID">
              <b-form-input
                type="text"
                v-model="zenDeskID"
                placeholder=""
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-3 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="date"
                v-model="claimDate"
                placeholder=""
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-3 mb-3">
            <b-input-group prepend=" ">
              <b-form-select
                v-model="claimDecide"
                :options="claimDecideOptions"
              ></b-form-select>
            </b-input-group>
          </div>
          <div class="col-md-3 mb-3">
            <b-input-group prepend=" ">
              <b-form-select
                v-if="claimDecide === '1. '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions1"
              ></b-form-select>
              <b-form-select
                v-else-if="claimDecide === '2. '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions2"
              ></b-form-select>
              <b-form-select
                v-else-if="claimDecide === '3. '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions3"
              ></b-form-select>
              <b-form-select
                v-else-if="claimDecide === '4. '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions4"
              ></b-form-select>
              <b-form-select
                v-else-if="claimDecide === '5. '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions5"
              ></b-form-select>
              <b-form-select
                v-else-if="claimDecide === '6. '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions6"
              ></b-form-select>
              <b-form-select
                v-else-if="claimDecide === '7.   '"
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions7"
              ></b-form-select>
              <b-form-select
                v-else
                v-model="claimDecideSideOptions"
                :options="claimDecideSideOptions8"
              ></b-form-select>
            </b-input-group>
          </div>
          <div class="col-md-3 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="date"
                v-model="lotDate"
                placeholder=""
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-3 mb-3">
            <b-input-group prepend=" ">
              <b-form-select
                v-model="recall"
                :options="recallOptions"
              ></b-form-select>
            </b-input-group>
          </div>
          <div class="col-md-9 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="text"
                v-model="asanaLink"
                placeholder=""
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend="">
              <b-form-textarea
                id="textarea-default"
                v-model="claimContent"
                rows="3"
                max-rows="5"
              ></b-form-textarea>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend="">
              <b-form-textarea
                id="textarea-default"
                v-model="specialReport"
                rows="3"
                max-rows="5"
              ></b-form-textarea>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend=" ">
              <b-form-file
                class="imgup"
                id="file-default"
                multiple
                @change="AddImageData"
                plain
                size="lg"
              ></b-form-file>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend=" ">
              <viewer
                :images="imgFiles"
                @inited="inited"
                v-for="(item, index) in imgFiles"
                :key="index"
              >
                <span
                  class="imgSizes"
                >
                  <button :class="[inputRead ? 'divEnable' : 'divDisable']"
                    class="xBox"
                    v-b-modal.xBox-modal
                    @click="imgElementSave(index)"
                  >
                    x
                  </button>
                  <img :src="item.url" />
                </span>
              </viewer>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend="">
              <b-form-textarea
                id="textarea-default"
                v-model="note"
                rows="3"
                max-rows="5"
              ></b-form-textarea>
            </b-input-group>
          </div>
        </div>
      </div>
      <div class="row" v-if="propsdata === 'insertView'">
        <div class="col-md-12 mb-3">
          <b-button
            variant="primary"
            class="rightBox"
            v-b-modal.insert-modal
            v-bind:disabled="skuNo === '' || claimDate === ''"
          >
              
          </b-button>
        </div>
      </div>
      <div class="row" v-else>
        <div class="col-md-12 mb-3" v-if="inputRead">
          <b-button variant="primary" class="rightBox" v-b-modal.update-modal>
              
          </b-button>
          <b-button variant="danger" class="rightBox" v-b-modal.delete-modal>
             
          </b-button>
        </div>
        <div class="col-md-12 mb-3" v-else>
          <b-button variant="primary" class="rightBox" @click="inpuReadMode">
             
          </b-button>
        </div>
      </div>

      <b-modal id="insert-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">  ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="insertClaim(), $bvModal.hide('insert-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('insert-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>

      <!--    -->
      <b-modal id="search-product-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">
          <select-product-table></select-product-table>
        </div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="insertProduct(), $bvModal.hide('search-product-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('search-product-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>

      <b-modal id="update-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">  ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="updateClaim(), $bvModal.hide('update-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('update-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>
      <b-modal id="delete-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">   ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="deleteClaim(), $bvModal.hide('delete-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('delete-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>

      <b-modal id="xBox-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">  ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="xBoxClick(), $bvModal.hide('xBox-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('xBox-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>
    </b-overlay>

    <!--  . -->
    <div>
      <common-modal @close="closeSearchModal" v-if="searchModal">
        <div v-if="modalName === 'searchProduct'">
          <select-product-table
            v-on:selectProductCheck="closeSearchModal"
          ></select-product-table>
          <template slot="footer">
            <button @click="modalText"></button>
          </template>
        </div>
      </common-modal>
    </div>

    <!--    -->
    <div>
      <confirm-modal @close="closeModal" v-if="modal">
        <div v-if="modalName === 'insertClaim'">
          <div v-if="insertState === 1">
            <p>  .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'updateClaim'">
          <div v-if="updateState === 1">
            <p>  .</p>
          </div>
          <div v-else>
            <p>   .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'deleteClaim'">
          <div v-if="deleteState === 1">
            <p> .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'noAdmin'">
          <p>   , ,   .</p>
        </div>

        <template
          slot="footer"
          v-if="modalName === 'insertClaim' && insertState === 1"
        >
          <b-button variant="primary" @click="roturInit('claimSel')"
            ></b-button
          >
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'updateClaim' && updateState === 1"
        >
          <b-button variant="primary" @click="roturInit('claimSel')"
            ></b-button
          >
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'deleteClaim' && deleteState"
        >
          <b-button variant="primary" @click="roturInit('claimSel')"
            ></b-button
          >
        </template>
        <template slot="footer" v-else>
          <b-button variant="primary" @click="modalText"></b-button>
        </template>
      </confirm-modal>
    </div>
  </div>
</template>

<script>
import commonModal from "../modal/commonModal.vue";
import SelectProductTable from "../select/SelectProductTable.vue";
import confirmModal from "../modal/ConfirmModal.vue";
import { modalMixin } from "../../mixins/modalMixin.js";
import { spinnerMixin } from "../../mixins/spinnerMixin.js";
import { imageMixin } from "../../mixins/imageMixin.js";
import { adminChkMixin } from "../../mixins/adminChkMixin.js";

export default {
  components: {
    commonModal,
    SelectProductTable,
    confirmModal,
  },
  mixins: [modalMixin, spinnerMixin, imageMixin, adminChkMixin],
  props: ["propsdata"],
  data() {
    return {
      claimId: "",
      productId: "",
      skuNo: "",
      productName: "",
      orderNum: "",
      zenDeskID: "",
      claimDate: "",
      claimDecide: "",
      claimDecideSideOptions: "",
      lotDate: "",
      recall: "",
      asanaLink: "",
      claimContent: "",
      specialReport: "",
      note: "",
      claimDecideOptions: [
        { value: "1. ", text: "1. " },
        { value: "2. ", text: "2. " },
        { value: "3. ", text: "3. " },
        { value: "4. ", text: "4. " },
        { value: "5. ", text: "5. " },
        { value: "6. ", text: "6. " },
        { value: "7.   ", text: "7.   " },
      ],
      claimDecideSideOptions1: [
        { value: "1) ", text: "1) " },
        { value: "2) ", text: "2) " },
      ],
      claimDecideSideOptions2: [
        { value: "1) ", text: "1) " },
        { value: "2) ", text: "2) " },
        { value: "3) ", text: "3) " },
        { value: "4)  ", text: "4)  " },
      ],
      claimDecideSideOptions3: [
        { value: "1) ", text: "1) " },
        { value: "2)  () ", text: "2)  () " },
        { value: "3)  ", text: "3)  " },
        { value: "4)  ", text: "4)  " },
      ],
      claimDecideSideOptions4: [
        { value: "1)  ", text: "1)  " },
        { value: "2) ", text: "2) " },
        { value: "3)  ", text: "3)  " },
      ],
      claimDecideSideOptions5: [
        { value: "1) ", text: "1) " },
        { value: "2) ", text: "2) " },
        { value: "3)  ", text: "3)  " },
      ],
      claimDecideSideOptions6: [
        { value: "1) ", text: "1) " },
        { value: "2) ", text: "2) " },
        { value: "3)  ", text: "3)  " },
      ],
      claimDecideSideOptions7: [{ value: "1) ", text: "1) " }],
      claimDecideSideOptions8: [{ value: "", text: " ." }],
      recallOptions: [
        { value: "", text: "" },
        { value: "", text: "" },
        { value: "", text: "" },
      ],
      confirmSkuNo: "",
      inputRead: true,
      skuNoDuplication: true,
      searchModal: false,
      insertState: 3,
      updateState: 3,
      deleteState: 3,
    };
  },
  mounted() {
    this.updateData();
  },
  computed: {
    //     
    selectProdut() {
      return this.$store.getters.getSkuProduct;
    },
  },
  watch: {
    //     
    selectProdut(val) {
      this.productId = val.productId;
      this.skuNo = val.skuNo;
      this.productName = val.productName;
    },
  },
  methods: {
    //  
    searchProduct() {
      this.searchModal = true;
      this.modalName = "searchProduct";
    },
    //    
    updateData() {
      if (this.propsdata === "updateView") {

        this.claimId = this.$store.state.getClaim.id;
        this.productId = this.$store.state.getClaim.product.id;
        this.skuNo = this.$store.state.getClaim.product.skuNo;
        this.productName = this.$store.state.getClaim.product.productName;
        this.orderNum = this.$store.state.getClaim.orderNum;
        this.zenDeskID = this.$store.state.getClaim.zenDeskID;
        this.claimDate = this.$store.state.getClaim.claimDate;
        this.claimDecide = this.$store.state.getClaim.claimDecide;
        this.claimDecideSideOptions =
          this.$store.state.getClaim.claimDecideSideOptions;
        this.lotDate = this.$store.state.getClaim.lotDate;
        this.recall = this.$store.state.getClaim.recall;
        this.asanaLink = this.$store.state.getClaim.asanaLink;
        this.claimContent = this.$store.state.getClaim.claimContent;
        this.specialReport = this.$store.state.getClaim.specialReport;
        this.note = this.$store.state.getClaim.note;
        this.inputRead = false;

        //    .
        if (this.$store.state.getClaim.imageFile.length > 0) {
          this.imgUpdate(this.$store.state.getClaim.imageFile);
        }
      } 
    },
    roturInit() {
      this.$router.push("/claimSel");
    },
    //     
    inpuReadMode() {
      this.inputRead = true;
    },
    //  
    insertClaim() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("claimStore/SET_INSERT_CLAIM", 3);

        this.imgFiles.forEach((element) => {
          element.forEach((e) => {
            this.formData.append("image", e["file"]);
          });
        });

        let data = {
          productId: this.productId,
          skuNo: this.skuNo,
          productName: this.productName,
          orderNum: this.orderNum,
          zenDeskID: this.zenDeskID,
          claimDate: this.claimDate,
          claimDecide: this.claimDecide,
          claimDecideSideOptions: this.claimDecideSideOptions,
          lotDate: this.lotDate,
          recall: this.recall,
          asanaLink: this.asanaLink,
          claimContent: this.claimContent,
          specialReport: this.specialReport,
          note: this.note,
        };

        this.formData.append(
          "data",
          new Blob([JSON.stringify(data)], { type: "application/json" })
        );
        this.formData.append(
          "productId",
          new Blob([JSON.stringify(this.productId)], {
            type: "application/json",
          })
        );

        this.$store
          .dispatch("claimStore/INSERT_CLAIM", this.formData)
          .then((response) => {
            this.modalName = "insertClaim";

            //   
            if (response.data === 1) {
              this.insertState = 1;
              this.openModal();
              this.closeSpinner();
            }
            //   
            else if (response.data === 0) {
              this.insertState = 0;
            }
          })
          .catch((error) => {
            console.log(error);
          });

        this.formData = new FormData();
      }
    },
    //  
    updateClaim() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("claimStore/SET_UPDATE_CLAIM", 3);

        this.imgFiles.forEach((element) => {
          element.forEach((e) => {
            if (e["file"]) {
              this.formData.append("image", e["file"]);
            } else if (e["imgId"]) {
              this.formData.append("imgId", e["imgId"]);
            }
          });
        });

        let data = {
          id: this.claimId,
          productId: this.productId,
          skuNo: this.skuNo,
          productName: this.productName,
          orderNum: this.orderNum,
          zenDeskID: this.zenDeskID,
          claimDate: this.claimDate,
          claimDecide: this.claimDecide,
          claimDecideSideOptions: this.claimDecideSideOptions,
          lotDate: this.lotDate,
          recall: this.recall,
          asanaLink: this.asanaLink,
          claimContent: this.claimContent,
          specialReport: this.specialReport,
          note: this.note,
        };

        this.formData.append(
          "data",
          new Blob([JSON.stringify(data)], { type: "application/json" })
        );
        this.formData.append(
          "productId",
          new Blob([JSON.stringify(this.productId)], {
            type: "application/json",
          })
        );

        this.$store
          .dispatch("claimStore/UPDATE_CLAIM", this.formData)
          .then((response) => {
            this.modalName = "updateClaim";

            //   
            if (response.data === 1) {
              this.updateState = 1;
              this.openModal();
              this.closeSpinner();
            }
            //   
            else if (response.data === 0) {
              this.updateState = 3;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            
            console.log(error);
          });
      }
    },
    //   
    deleteClaim() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("claimStore/SET_DELETE_CLAIM", 3);

        this.$store
          .dispatch("claimStore/DELETE_CLAIM", { id: this.claimId })
          .then((response) => {
            this.modalName = "deleteClaim";
            //   
            if (response.data === 1) {
              this.deleteState = 1;
              this.openModal();
              this.closeSpinner();
            }
            //   
            else if (response.data === 0) {
              this.deleteState = 0;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            
            console.log(error);
          });
      }
    },
  },
};
</script>

<style scoped>
.rightBox {
  float: right;
  margin-left: 10px;
}
.divDisable {
  pointer-events: none;
}
.divEnable {
  pointer-events: auto;
}
.imgSizes {
  width: 100px;
  height: 100px;
  margin-left: 10px;
}
.imgup {
  margin-left: 10px;
}
</style>