import axios from "axios";

// 1. Http Request & Response   
const authUrl = "/auth/";


//  
function userLogin(user) {
  let data = {
    email: user.userId,
    password: user.userPassword,
  };

  return axios.post(authUrl + "login", data, {
    header: { "Content-Type": `application/json` },
  });
}

export {
    userLogin
}
