<template>
  <div>
    <b-overlay :show="spinnerState" rounded="sm">
      <div :class="[inputRead ? 'divEnable' : 'divDisable']">
        <div class="row">
          <div class="col-md-3 mb-3">
            <b-input-group prepend="">
              <b-form-input
                type="text"
                v-model="makerName"
                placeholder=""
                value=""
                maxlength="100"
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-3 mb-3">
            <b-input-group prepend="(100 )">
              <b-form-input
                type="text"
                v-model="makerScore"
                placeholder=""
                value=""
                maxlength="3"
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-6 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="text"
                v-model="makerAddress"
                placeholder=""
                value=""
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-3 mb-3">
            <b-input-group prepend="">
              <b-form-select
                v-model="className"
                :options="options"
              ></b-form-select>
            </b-input-group>
          </div>
          <div class="col-md-3 mb-3">
            <b-input-group prepend="()">
              <b-form-input
                type="text"
                v-model="sales"
                placeholder=""
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-6 mb-3">
            <b-input-group prepend="">
              <b-form-input
                type="text"
                v-model="process"
                placeholder=""
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-3 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="text"
                v-model="makerPerson"
                placeholder=""
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-4 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="text"
                v-model="makerPhone"
                placeholder=" ."
                maxlength="14"
                oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
              ></b-form-input>
            </b-input-group>
          </div>
          <div class="col-md-5 mb-3">
            <b-input-group prepend=" ">
              <b-form-input
                type="text"
                v-model="makerEmail"
                placeholder=""
              ></b-form-input>
            </b-input-group>
          </div>
        </div>
        <div class="col-md-12 mb-3">
            <b-input-group prepend="">
              <b-form-textarea
                id="textarea-default"
                v-model="importProduct"
                rows="3"
                max-rows="10"
              ></b-form-textarea>
            </b-input-group>
          </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend=" ">
              <b-form-textarea
                id="textarea-default"
                v-model="makerInfo"
                rows="3"
                max-rows="10"
              ></b-form-textarea>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend="">
              <b-form-textarea
                id="textarea-default"
                v-model="note"
                rows="3"
                max-rows="5"
              ></b-form-textarea>
            </b-input-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12 mb-3">
            <b-input-group prepend=" ">
              <b-form-textarea
                id="textarea-default"
                v-model="makerChangeContent"
                rows="3"
                max-rows="5"
              ></b-form-textarea>
            </b-input-group>
          </div>
        </div>
      </div>
      <div class="row" v-if="propsdata === 'insertView'">
        <div class="col-md-12 mb-3">
          <b-button
            variant="primary"
            class="rightBox"
            v-b-modal.insert-modal
            v-bind:disabled="makerName === '' || className === ''"
          >
              
          </b-button>
        </div>
      </div>
      <div class="row" v-else>
        <div class="col-md-12 mb-3" v-if="inputRead">
          <b-button variant="primary" class="rightBox" v-b-modal.update-modal>
              
          </b-button>
          <b-button variant="danger" class="rightBox" v-b-modal.delete-modal>
             
          </b-button>
        </div>
        <div class="col-md-12 mb-3" v-else>
          <b-button variant="primary" class="rightBox" @click="inpuReadMode">
             
          </b-button>
        </div>
      </div>
<!-- v-if="propsdata === 'updateView'" -->
      <div v-if="propsdata === 'updateView'">
        <div class="leftTable">
          <div class="row">
            <div class="col-md-6">
              <p>  (     )</p>
            </div>
            <div class="col-md-6">
              <b-button
                variant="primary"
                class="rightBox"
                v-b-modal.reply-update-modal
              >
                  
              </b-button>
              <b-button
                variant="danger"
                class="rightBox"
                v-b-modal.reply-delete-modal
              >
                  
              </b-button>
            </div>
          </div>
          
          <div class="replytable">
            <b-table
              sticky-header
              responsive
              striped
              hover
              :items="makerReply"
              :fields="histioryFields"
              label-sort-asc=""
              label-sort-desc=""
              label-sort-clear=""
            >
              <template v-slot:head(selected)>
                <b-form-checkbox></b-form-checkbox>
              </template>
              <template v-slot:cell(selected)="row">
                <b-form-checkbox
                  v-model="row.item.selected"
                  @change="selectReplyData(row.item)"
                ></b-form-checkbox>
              </template>

              <!--    -->
              <template v-slot:cell(makerChangeReply)="row">
                <b-form-input v-model="row.item.makerChangeReply" />
              </template>
            </b-table>
          </div>
        </div>
        <div class="rightTable">
          <div class="row">
            <div class="col-md-6">
              <p>  (,   )</p>
            </div>
            <div class="col-md-6">
              <b-button
                variant="primary"
                class="rightBox"
                @click="addAudit()"
              >
                  
              </b-button>
            </div>
          </div>
          <div>
            <b-table
              sticky-header
              responsive
              striped
              hover
              :items="makerAudit"
              :fields="auditFields"
              label-sort-asc=""
              label-sort-desc=""
              label-sort-clear=""
              @row-dblclicked="makerAuditDetails"
            >
            </b-table>
          </div>
        </div>
      </div>
      <b-modal id="insert-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">  ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="insertMaker(), $bvModal.hide('insert-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('insert-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>

      <b-modal id="reply-update-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">  ?</div>
        <div>    .</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="updateMakerReply(), $bvModal.hide('reply-update-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('reply-update-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>
      <b-modal id="reply-delete-modal">
        <!-- <template #modal-title>   </template> -->

        <div class="my-4">  ?</div>
        <div>    .</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="deleteMakerReply(), $bvModal.hide('reply-delete-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('reply-delete-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>

      <!--   -->
      <b-modal id="search-product-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">
          <select-product-table></select-product-table>
        </div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="insertProduct(), $bvModal.hide('search-product-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('search-product-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>

      <b-modal id="update-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4">  ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="updateMaker(), $bvModal.hide('update-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('update-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>
      <b-modal id="delete-modal">
        <!-- <template #modal-title>   </template> -->
        <div class="my-4"> ?</div>
        <template #modal-footer>
          <div class="d-block text-center">
            <b-button
              variant="primary"
              class="frightBox"
              @click="deleteMaker(), $bvModal.hide('delete-modal')"
              :disabled="spinnerState"
            >
              
            </b-button>
            <b-button
              variant="primary"
              class="rightBox"
              @click="$bvModal.hide('delete-modal')"
            >
              
            </b-button>
          </div>
        </template>
      </b-modal>
    </b-overlay>

    <!--   . -->
    <div>
      <common-modal @close="closeSearchModal" v-if="searchModal">
        <div v-if="modalName === 'addAudit'">
          <maker-audit-input-list :propsdata="[messageId, messageMode]"></maker-audit-input-list>
          <template slot="footer">
            <button @click="modalText"></button>
          </template>
        </div>
      </common-modal>
    </div>


    <!--    -->
    <div>
      <confirm-modal @close="closeModal" v-if="modal">
        <div v-if="modalName === 'insertMaker'">
          <div v-if="insertState === 1">
            <p>  .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'updateMaker'">
          <div v-if="updateState === 1">
            <p>  .</p>
          </div>
          <div v-else>
            <p>   .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'deleteMaker'">
          <div v-if="deleteState === 1">
            <p>  .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'updateMakerReply'">
          <div v-if="updateState">
            <p>  .</p>
          </div>
          <div v-else>
            <p>   .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'deleteMakerReply'">
          <div v-if="deleteState">
            <p>  .</p>
          </div>
          <div v-else>
            <p>   .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'noAdmin'">
          <p>   , ,   .</p>
        </div>

        <!-- <template slot="footer">
          <button @click="modalText"></button>
        </template> -->
        <template
          slot="footer"
          v-if="modalName === 'insertMaker' && insertState === 1"
        >
          <b-button variant="primary" @click="roturInit('makerSel')"
            ></b-button
          >
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'updateMaker' && updateState === 1"
        >
          <b-button variant="primary" @click="roturInit('makerSel')"
            ></b-button
          >
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'deleteMaker' && deleteState"
        >
          <b-button variant="primary" @click="roturInit('makerSel')"
            ></b-button
          >
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'updateMakerReply' && updateState"
        >
          <button @click="roturInit('makerSel')"></button>
        </template>
        <template
          slot="footer"
          v-else-if="modalName === 'deleteMakerReply' && deleteState"
        >
          <button @click="roturInit('makerSel')"></button>
        </template>
        <template slot="footer" v-else>
          <b-button variant="primary" @click="modalText"></b-button>
        </template>
      </confirm-modal>
    </div>
  </div>
</template>

<script>
import commonModal from "../modal/commonModal.vue"
import makerAuditInputList from "../inpuList/makerAuditInputList.vue"
import confirmModal from "../modal/ConfirmModal.vue";
import { modalMixin } from "../../mixins/modalMixin.js";
import { spinnerMixin } from "../../mixins/spinnerMixin.js";
import { adminChkMixin } from "../../mixins/adminChkMixin.js";
// import ss from '../'

export default {
  components: {    
    commonModal,
    confirmModal,
    makerAuditInputList
  },
  mixins: [modalMixin, spinnerMixin, adminChkMixin],
  props: ["propsdata"],
  data() {
    return {
      makerId: "",
      className: "",
      makerName: "",
      makerAddress: "",
      process: "",
      importProduct: "",
      makerScore: "",
      sales: "",
      makerInfo: "",
      makerPerson: "",
      makerPhone: "",
      makerEmail: "",
      note: "",
      makerChangeContent: "",
      makerReply: [],
      selectReply: [],
      makerAudit:[],
      options: [
        { value: "", text: "1. " },
        { value: " ", text: "2.  " },
        { value: " ", text: "3.  " },
        { value: "", text: "4. " },
        { value: "", text: "5. " },
        { value: "", text: "6. " },
        { value: "", text: "7. " },
      ],
      histioryFields: [
        { key: "selected", thClass: "w2" },
        {
          key: "makerChangeReply",
          label: " ",
          sortable: true,
          thClass: "w50",
        },
        { key: "replyDate", label: "", sortable: true, thClass: "w15" },
      ],
      auditFields: [
        {
          key: "auditDate",
          label: "",
          sortable: true,
          thClass: "w15",
        },
        { key: "auditPurpose", label: "", sortable: true, thClass: "w40" },
        { key: "auditScore", label: "", sortable: true, thClass: "w20" },
      ],
      inputRead: true,
      searchModal: false,
      insertState: 3,
      updateState: 3,
      deleteState: 3,
      modalName: "",
      messageId: "",
      messageMode:""
    };
  },
  mounted() {
    this.updateData();
  },

  methods: {
    //    
    updateData() {
      if (this.propsdata === "updateView") {
        this.makerId = this.$store.state.getMaker.id;
        this.makerName = this.$store.state.getMaker.makerName;
        this.className = this.$store.state.getMaker.className;
        this.makerScore = this.$store.state.getMaker.makerScore;
        this.makerInfo = this.$store.state.getMaker.makerInfo;        
        this.makerAddress = this.$store.state.getMaker.makerAddress;
        this.process = this.$store.state.getMaker.process;
        this.importProduct = this.$store.state.getMaker.importProduct;
        this.sales = this.$store.state.getMaker.sales;
        this.makerPerson = this.$store.state.getMaker.makerPerson;
        this.makerPhone = this.$store.state.getMaker.makerPhone;
        this.makerEmail = this.$store.state.getMaker.makerEmail;
        this.note = this.$store.state.getMaker.note;
        this.makerReply = this.$store.state.getMaker.makerChangeReplies;
        this.inputRead = false;
        this.messageId = this.$store.state.getMaker.id;
        this.makerAudit = this.$store.state.getMaker.makerAuditList;

        this.makerReply.forEach((e) => {
          let tmpData = {
            id: e.id,
            makerChangeReply: e.makerChangeReply,
            replyDate: e.replyDate,
            selected: false,
            createDate: e.createDate,
          };
          this.selectReply.push(tmpData);
        });
      } else {
        this.imgFlag = true;
      }
    },
    //   
    addAudit() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      } else{
        this.messageMode="insertView"
        this.searchModal = true;
        this.modalName = "addAudit";
      }
    },
    //  
    selectReplyData(item) {
      this.selectReply.forEach((element, index) => {
        if (element.id === item.id) {
          this.selectReply[index].selected = item.selected;
        }
      });
    },
    // ,   
    inpuReadMode() {
      this.inputRead = true;
    },
    //   
    makerAuditDetails(item) {
      this.$store.commit("SET_MAKER_AUDIT", "");

      //     
      this.$store.commit("SET_MAKER_AUDIT", item);
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      } else{
        this.messageMode="updateView"
        this.searchModal = true;
        this.modalName = "addAudit";
      }
      // this.$router.push(`/makerUp/${item.id}`);
    },
    //  
    insertMaker() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("makerStore/SET_INSERT_MAKER", 3);

        let data = {
          id: this.makerId,
          makerName: this.makerName,
          className: this.className,
          makerAddress: this.makerAddress,
          process: this.process,
          importProduct: this.importProduct,
          sales: this.sales,
          makerScore : this.makerScore,
          makerInfo : this.makerInfo,
          makerPerson: this.makerPerson,
          makerPhone: this.makerPhone,
          makerEmail: this.makerEmail,
          note: this.note,
          makerChangeContent : this.makerChangeContent
        };
        this.$store
          .dispatch("makerStore/INSERT_MAKER", data)
          .then((response) => {
            this.modalName = "insertMaker";

            //   
            if (response.data === 1) {
              this.insertState = 1;
              this.$store.commit("makerStore/SET_SELECT_MAKER_PRODUCT", []);
              this.openModal();
              this.closeSpinner();
            }
            //   
            else if (response.data === 0) {
              this.insertState = 0;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    //  
    updateMaker() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("makerStore/SET_UPDATE_MAKER", 3);

        let data = {
          id: this.makerId,
          makerName: this.makerName,
          className: this.className,
          makerAddress: this.makerAddress,
          process: this.process,
          importProduct: this.importProduct,
          makerScore : this.makerScore,
          makerInfo : this.makerInfo,
          sales: this.sales,
          makerPerson: this.makerPerson,
          makerPhone: this.makerPhone,
          makerEmail: this.makerEmail,
          makerChangeContent : this.makerChangeContent,
          note: this.note,
        };

        this.$store
          .dispatch("makerStore/UPDATE_MAKER", data)
          .then((response) => {
            this.modalName = "updateMaker";

            //   
            if (response.data === 1) {
              this.updateState = 1;
              this.$store.commit("makerStore/SET_SELECT_MAKER_PRODUCT", []);
              this.openModal();
              this.closeSpinner();
            }
            //   
            else if (response.data === 0) {
              this.updateState = 3;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    //  
    deleteMaker() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("makerStore/SET_DELETE_MAKER", 3);

        this.$store
          .dispatch("makerStore/DELETE_MAKER", { id: this.makerId })
          .then((response) => {
            this.modalName = "deleteMaker";
            //   
            if (response.data === 1) {
              this.deleteState = 1;
              this.$store.commit("makerStore/SET_SELECT_MAKER_PRODUCT", []);
              this.openModal();
              this.closeSpinner();
            }
            //   
            else if (response.data === 0) {
              this.deleteState = 0;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    //  
    updateMakerReply() {
      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();
        this.$store.commit("makerStore/SET_UPDATE_MAKER_REPLY", "3");

        var tmpReply = [];
        this.makerReply.forEach((element) => {
          if (element.selected) {
            tmpReply.push({replyId : element.id, changeReplyData : element.makerChangeReply});
          }
        });

        let data = {
          id: this.makerId,
          replyDataList: tmpReply,
        };

        this.$store
          .dispatch("makerStore/UPDATE_MAKER_REPLY", data)
          .then((response) => {
            this.modalName = "updateMakerReply";

            if (response.data === 1) {
              this.updateState = true;
              this.openModal();
              this.closeSpinner();
            } else if (response.data === 0) {
              this.updateState = false;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },

    //   
    deleteMakerReply() {

      //     
      // adminChkMixin 
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";

        this.openModal();
      }
      //    
      else {
        this.openSpinner();

        var tmpReply = [];
        this.makerReply.forEach((element) => {
          if (element.selected) {
            tmpReply.push(element.id);
          }
        });

        let data = {
          makerId: this.makerId,
          makerReplyId: tmpReply,
        };

        this.$store.commit("makerStore/SET_DELETE_MAKER_REPLY", "3");
        this.$store
          .dispatch("makerStore/DELETE_MAKER_REPLY", data)
          .then((response) => {
            this.modalName = "deleteMakerReply";

            if (response.data === 1) {
              this.deleteState = true;
              this.openModal();
              this.closeSpinner();
            } else if (response.data === 0) {
              this.deleteState = false;
              this.openModal();
              this.closeSpinner();
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
};
</script>

