<template>
  <div class="body">
    <b-overlay :show="spinnerState" rounded="sm">
      <div class="title"> </div>
      <select-maker
        :propsdata="message"
        @spinnerStart="openSpinner"
      ></select-maker>
      <div class="row">
        <div class="col-md-12">
          <b-button variant="primary" class="right-box" v-on:click="download"
            ></b-button
          >
        </div>
      </div>
      <div class="selectTableCss" style="height: 68vmin;">
        <b-table
          sticky-header="100vh"
          responsive
          striped
          hover
          :items="getSelectMaker"
          :fields="fields"
          label-sort-asc=""
          label-sort-desc=""
          label-sort-clear=""
          @row-dblclicked="makerDetails"
        ></b-table>
      </div>
    </b-overlay>

    <!-- . -->
    <div>
      <confirm-modal @close="closeModal" v-if="modal">
        <div v-if="modalName === 'excelDataDown'">
          <div v-if="excelDownState">
            <p>  .</p>
          </div>
          <div v-else>
            <p>  .   .</p>
          </div>
        </div>
        <div v-if="modalName === 'noAdmin'">
          <p>       .</p>
          <p>  .</p>
        </div>

        <template slot="footer">
          <button @click="modalText"></button>
        </template>
      </confirm-modal>
    </div>
  </div>
</template>

<script>
import SelectMaker from "../../components/select/SelectMaker.vue";
import confirmModal from "../../components/modal/ConfirmModal.vue";
import { adminChkMixin } from "../../mixins/adminChkMixin.js";
import { modalMixin } from "../../mixins/modalMixin.js";
// import * as XLSX from "xlsx";
// import { mapGetters } from 'vuex'

export default {
  components: {
    SelectMaker,
    confirmModal
  },
  mixins: [modalMixin, adminChkMixin],
  data() {
    return {
      message: "testSelView",
      getSelectMaker: [],
      fields: [
        { key: "makerName", label: "", sortable: true, thClass: "w10" },
        { key: "className", label: "", sortable: true, thClass: "w10" },
        {
          key: "importProduct",
          label: "",
          sortable: true,
          thClass: "w20",
        },
        { key: "process", label: "", sortable: true, thClass: "w15" },
        { key: "sales", label: "", sortable: true, thClass: "w10" },
        {
          key: "makerPerson",
          label: " ",
          sortable: true,
          thClass: "w10",
        },
        {
          key: "makerPhone",
          label: " ",
          sortable: true,
          thClass: "w15",
        },
      ],
      spinnerState: false,
    };
  },
  created() {},
  mounted() {
    this.openSpinner();
  },
  computed: {
    // ...mapGetters('makerStore', [
    //   'getSelectMaker'
    // ]),
    SelectMaker() {
      return this.$store.getters["makerStore/getSelectMaker"];
    },
  },
  watch: {
    //     
    SelectMaker(val) {
      this.getSelectMaker = val;
      this.closeSpinner();
    },
  },
  methods: {
    // 
    download() {
      this.adminChk();
      if (this.adminChkFlag) {
        this.modalName = "noAdmin";
      }
      else{
        // const workBook = XLSX.utils.book_new();
        // const workSheet = XLSX.utils.json_to_sheet(this.getSelectMaker);
        // XLSX.utils.book_append_sheet(workBook, workSheet, " ");
        // XLSX.writeFile(workBook, " .xlsx");
        try {
          this.modalName = "excelDataDown";
          /** exceljs  */
          const ExcelJS = require('exceljs');
          const workbook = new ExcelJS.Workbook();
          const sheet = workbook.addWorksheet(' ');

          //  
          // header:   
          // key:    key
          // hidden:  
          // width:  
          sheet.columns = [
            {header: '', key: 'makerName', hidden:false, width: 20,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: '', key: 'className', width: 15,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: ' ', key: 'makerAddress', width: 30,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: '', key: 'process', width: 20,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: '', key: 'importProduct', width: 25,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: '()', key: 'sales', width: 15,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: '(100 )', key: 'makerScore', width: 10,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: ' ', key: 'makerPerson', width: 20,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: ' ', key: 'makerPhone', width: 15,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: ' ', key: 'makerEmail', width: 30,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: ' ', key: 'makerInfo', width: 50,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
            {header: '', key: 'note', width: 50,
              style: {
                border: {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'},
                },
              }
            },
          ]

          console.log(this.getSelectMaker);

          this.getSelectMaker.map((item) =>{
            sheet.addRow(item);
          })

          sheet.getCell('A1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('B1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('C1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('D1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('E1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('F1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('G1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('H1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('I1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('J1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('K1').alignment = { vertical: 'middle', horizontal: 'center' };
          sheet.getCell('L1').alignment = { vertical: 'middle', horizontal: 'center' };

          sheet.getCell('A1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('B1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('C1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('D1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('E1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('F1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('G1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('H1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('I1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('J1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('K1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};
          sheet.getCell('L1').fill ={type: 'pattern', pattern:'solid', fgColor:{argb:'FFE6E6E6'}};

          workbook.xlsx.writeBuffer().then((data) => {
            const blob = new Blob([data], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
            const url = window.URL.createObjectURL(blob);
            const anchor = document.createElement('a');
            anchor.href = url;
            anchor.download = ` .xlsx`;
            anchor.click();
            window.URL.revokeObjectURL(url);
          })

          this.excelDownState = true;
          this.closeSpinner();
          this.openModal();
        } catch (error) {
          this.excelDownState = false;
              console.log(error);
              this.openModal();
        }
      }
      
    },
    //   
    makerDetails(item) {
      this.$store.commit("SET_MAKER", "");

      //         
      this.$store.commit("SET_MAKER", item);
      this.$router.push(`/makerUp/${item.id}`);
    },
    //  
    openSpinner() {
      this.spinnerState = true;
    },
    //  
    closeSpinner() {
      this.spinnerState = false;
    },
  },
};
</script>

<style scoped>
.right-box {
  float: right;
  width: auto;
}
</style>